.header{
    max-height: 200px;
    min-height: 100px;
    padding-top: 50px;
    padding-bottom: 50px;
}
.container{

justify-content: center;
padding-top: 25px;
background: rgba(234, 234, 248, 0.541);
display: flex;
padding: 15px;
overflow: auto;

}
.card {
    background: rgba(255, 255, 255, 0.61);
    height: 175px;
    margin: 2rem;
    position: relative;
    width: 100px;
    text-align: left;
  }

  .img-thumbnail{
    margin:0 auto;
    height:215px;
    width:100%;
    object-fit: cover;
  }

  .image{

    box-sizing: border-box;
  }
